.slider {
	position: relative;
}

.jcarousel {
	position: relative;
	overflow: hidden;
}

.jcarousel ul {
	width: 20000em;
	position: relative;
	list-style: none;
	margin: 0;
	padding: 0;
}

.jcarousel li {
	float: left;
}

/** Carousel Controls **/

.jcarousel-control-prev,
.jcarousel-control-next {
	position: absolute;
	top: 200px;
	width: 30px;
	height: 30px;
	text-align: center;
	background: #4E443C;
	color: #fff;
	text-decoration: none;
	text-shadow: 0 0 1px #000;
	font: 24px/27px Arial, sans-serif;
	border-radius: 30px;
	box-shadow: 0 0 2px #999;
}

.jcarousel-control-prev {
	left: -50px;
}

.jcarousel-control-next {
	right: -50px;
}

.jcarousel-control-prev:hover span,
.jcarousel-control-next:hover span {
	display: block;
}

.jcarousel-control-prev.inactive,
.jcarousel-control-next.inactive {
	opacity: .5;
	cursor: default;
}

/** Carousel Pagination **/

.jcarousel-pagination {
	position: absolute;
	bottom: 17px;
	left: 50%;
	transform: translateX(-50%);
}

.jcarousel-pagination a {
	text-decoration: none;
	display: inline-block;
	font-size: 0;
	line-height: 10px;
	min-width: 10px;
	background: #fff;
	color: #fff;
	border-radius: 14px;
	padding: 3px;
	text-align: center;
	margin-right: 5px;
	opacity: .75;
}

.jcarousel-pagination a.active {
	background: #4E443C;
	color: #fff;
	opacity: 1;
	text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.75);
}
